nav {
    background: rgb(0, 0, 0, 9);
    padding: 20px;
    display: flex;
    justify-content: space-between;
    color: #fff;
    position: relative;
    z-index: 99;
}
.menu-icon {
    display: none;
}
.logo {
    font-weight: 600;
    font-size: 25px;
}

.logo font {
    color: #fff;
    font-weight: 300;
}

.menu-list {
    list-style-type: none;
    display: flex;
    justify-content: center;
}

.menu-list li a {
    text-decoration: none;
    color: #fff;
    padding: 5px 10px;
    margin-left: 20px;
}

.menu-list li:last-child a {
    font-weight: 300;
    padding: 5px 10px;
    background-color: #ffffff;
    color: black;
    /* transition: all 0.5 ease-in-out; */
    margin-left: 20px;
    border-radius: 2px;
} 

.menu-list li:last-child a:hover,
.menu-list li:last-child a.active {
    background-color: #000000;
    color: rgb(255, 255, 255); 
    border: 1px solid white;  
    transition: all 0.5 ease-in-out;
}

.menu-list li a:hover,
.menu-list li a.active {
    border-bottom: 1px solid #fff;
    /* border-radius: 2px; */
    /* color: #000; */
    transition: all 0.5s ease-in-out;
}

@media screen and (max-width: 800px) {
  .logo {
      font-size: 20px;
  } 
  .menu-list li a {
      font-size: 14px;
      margin-left: 10px;
  }
}

@media screen and (max-width: 600px) {
    .menu-icon {
        display: block;
    }

    .fas {
        cursor: none;
    }
    .menu-icon .fa-times {
        transform: rotate(180deg);
        transition: all 0.2s ease-in-out;
    }

    .menu-icon .fa-bars {
        transform: rotate(180deg);
        transition: all 0.2s ease-in-out;
    }
    .menu-list {
        flex-direction: column;
        width: 100%;
        position: absolute;
        background-color: rgba(0, 0, 0, 0.7);
        padding: 20px;
        right: 0;
        top: 64px;
    }

    .menu-list li {
        margin-bottom: 20px;
        width: 100%;
        text-align: center;
    }

    .menu-list li:last-child {
        margin: 0;
    }

    .menu-list li a,
    .menu-list li:last-child a {
        width: 100px;
        display: block;
        margin: auto;
    }
    .menu-list.close {
        right: -100%;
        transition: all 0.5s ease-in-out;
    }
}